		methods: {
			hideModalcard()
			{
				if(this.hasUserInfo==true && this.modalCard==true)
				{
					this.modalCard=false;
				}
			},
			getUserInfo(){
				var that =this;
				wx.authorize({
					scope:"scope.userInfo",
					
					complete(e){
						uni.getSetting({
						  success (res) {
							  if(res.authSetting["scope.userInfo"]){
									
									that.onGetUserInfo();
							  }else{
								  
								  wx.showToast({
								  	"title":"授权失败,请重新授权",
								  	"icon":"none"
								  })
							  }
						  }
						})
					}
				})
			},
			
			onGetUserInfo() {
			    //const userInfo = event.detail.userInfo
				   /*  wx.showLoading(); */
			         var that=this; 
					
			        wx.login({
			            success: function (login_res) {
			                wx.getUserInfo({
			                    success: function (res) {
								/* 	console.log(2222222);
									console.log(login_res);
									console.log(res); */
									var userInfo = res.userInfo;
			                        wx.request({
			                            url: that.$server+'/api/me/login',
			                            method: 'POST',
										withCredentials:true,
			                            header: {
			                                'content-type': 'application/x-www-form-urlencoded'
			                            },
			                            data: {
			                                code:login_res.code,
			                                userHead: userInfo.avatarUrl,
			                                userName: userInfo.nickName,
			                                userGender: userInfo.gender,
			                                userCity: userInfo.city,
			                                userProvince: userInfo.province
			                            },
			                            success:function(res) {
			                               /* var userInfo = res.data.data;
											
											console.log(userInfo); */
											
											that.hasUserInfo=true;
											that.userInfo=userInfo;
											
											uni.hideLoading();
											that.modalCard=true;
			                                // 将返回的数据保存到全局的缓冲中，方便其他页面使用
											
			                                wx.setStorageSync( "sessionid", res.data.data)
											
			                            }
			                        })
			                    },
								fail:function(res){
									
									
									uni.hideLoading();
									that.modalCard=true;
									
									/* wx.showToast({
										"title":"请检查网络连接",
										"icon":"none"
									}) */
									/* console.log(444444444);
									console.log(res); */
								}
			                })
			            },
						fail:function(res){
							
							uni.hideLoading();
							that.modalCard=true;
							
						}
						
			        })
			        /* this.setData({
			            hasUserInfo: true,
			            userInfo: userInfo
			        }) */
			    //}
			},
			checkAuth()
			{
			 uni.showLoading({title:"加载中"});
			 var that=this;	 
			 
			  uni.getSetting({
			    success (res) {
			  	  if(res.authSetting["scope.userInfo"]){
					    that.subtitle="欢迎";
			  			that.onGetUserInfo();
						
			  	  }else{
					 
			  		   that.subtitle="授权";
					  
					   uni.hideLoading();
					   that.modalCard=true;
			  	  }
			    },
				fail(res){
					 uni.hideLoading();
					console.log(res);
				}
			  })	
			  
				
