								  		dirPath:wx.env.USER_DATA_PATH + "/"+dir,
										recursive: false,
								  		success(res){
								  			
											manage.saveFile({
											  tempFilePath: tempFilePath,
											  filePath: wx.env.USER_DATA_PATH + "/"+dir +"/"+ fileName,
											  success: function(res) {
												  
												 that.downloaded=true;
												 uni.hideLoading();
												 uni.showToast({
												 	icon:"none",
												 	title:"下载成功"
												 })
											  },
											fail(res){
												uni.hideLoading();
												uni.showToast({
													icon:"none",
													title:"下载失败,请重试"
												});
											}
											});
											
											
											
								  		},
								  		fail(res){
											uni.hideLoading();
								  			uni.showToast({
								  				icon:"none",
								  				title:"下载失败,请重试"
								  			});
								  		}
								  	});
							 
							    }else{
									uni.hideLoading();
									uni.showToast({
										icon:"none",
										title:"下载失败,请重试"
									});
								}
								
								
								
								
							   
							  }
							});
							
							
							
							 /* const downloadTask = uni.downloadFile({
							    url: url, 
							    success: (res) => {
							        if (res.statusCode === 200) {
							            uni.saveFile({
							                tempFilePath: res.tempFilePath,
							                    success: function(result) {
							                        //that.savedFilePath = res.savedFilePath;
													that.flag=true;
													uni.hideLoading();
													uni.showToast({
														icon:"none",
														title:"下载成功"
													})
													
													
												   
							                   },
							            		fail:function(res)
							            		{
													uni.showToast({
														icon:"none",
														title:"下载失败"
													})
							            	      
							            		}
							                });
										
							        }else{
										uni.showToast({
											icon:"none",
											title:"下载失败"
										})
									}
			
							        let that = this;
							       
