									fail(res){
										uni.showToast({
											icon:"none",
											title:"下载失败"
										})
									}
							    }); */
								
								
			
						}else{
							uni.hideLoading();
							uni.showToast({
								icon:"none",
								title:"下载失败"
							})
						}
					},
					fail(res) {
						uni.hideLoading();
						console.log(res);
						uni.showToast({
							icon:"none",
							title:"网络连接失败"
						})
					},
					complete() {
			
