			menu(i){
				switch(i){
					case 0: Util.jump('/pages/user/history');break;
					case 1: this.to(0);break;
					case 2: this.to(1);break;
					case 3: this.to(2);break;
					case 4:Util.jump('/pages/exam/download'); break;
				}
				
			},
			to(type){
				var that=this;
				var url=this.$server+"/question/get?type="+type;
				 uni.request({
					url:url ,
					header:{'Cookie':"JSESSIONID="+that.JSESSIONID()},
					withCredentials:true,
					success(res){
						console.log(res.data);
						if(res.data.code==200){
							       
							       if(res.data.data!=null){
							       Util.jump('/pages/user/questions?type='+type+'&pn=0');
								   }
								   }else{
									   uni.showToast({
									   	icon:"none",
										title:"无数据"
									   })
								   }
					},
					fail(res){
						uni.showToast({
							icon:"none",
							title:"无数据"
						})
					}
					
				});
			},
			open(item) {
				item.visible = true;
			},
			submit() {
				this.$refs["confirm"].open({
					title: "提示",
					message: "确定添加该条数据吗？",
					callback: ({ action }) => {
						if (action === "confirm") {
							this.visible = false;
						}
					},
				});
			},
			
			exam(i)
			{
				var that=this;
				if(i==0 || i==1){
				uni.request({
						url: this.$server+"/exam/get?type="+(i+1),
						method: "GET",
						dataType: 'json',
						header:{'Cookie':"JSESSIONID="+that.JSESSIONID()},
						success(res) {
							if(res.data.code==200)
							{
								var eid=null;
								if(res.data.data!=null){
								eid=res.data.data.id;
								}
								
								if(eid!=null){
								if(i==0){
								 that.paper=res.data.data;	
								 that.examshow=true;
								 console.log(that.examshow);   
								 
								}else{
									
									Util.jump('/pages/exam/start?eid='+eid);
								}
								}else{
									uni.showToast({
										icon:"none",
										title:"无数据"
									})
								}
							}else{
								uni.showToast({
									icon:"none",
									title:"出现问题了"
								})
								
							}
						},
						fail(res) {
							uni.showToast({
								icon:"none",
								title:"出现问题了"
							})
							
						},
						complete() {
							
						}
					});
					}else if(i==2){
						
						uni.request({
							url: this.$server+"/exam/zt",
							withCredentials:true,
							success(res){
								if(res.data.code==200){
								 uni.hideLoading();	
								 if(res.data.data!=null && res.data.data.length>0)
								 {
									 
									Util.jump('/pages/exam/zt');
								 }else{
									 
									 uni.showToast({
									 	icon:"none",
									 	title:"无数据"
									 })
								 }
								}else{
									
									 uni.showToast({
										icon:"none",
										title:"出现问题了"
									})
								}
							},
							fail(res){
								
								uni.showToast({
									icon:"none",
									title:"出现问题了"
								})
							}
							
						});
					
					}else{
						Util.jump('/pages/knowledge/index');
					}
				}
					
				
			}
			}

	
